.section, .section-header {
  @extend %wrapper;
}

.page-main-container, .section-outer-container, .section-header-container, .section-container {
  @extend %container;
}

.page-main {
  &-container {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  &.light {
    background: #e9e9e9;
    color: #222;
  }
  
  table {
    border: 1px solid #aaa;
    border-collapse: collapse;
    width: 100%;
  
    @include subpixeled;
    
    th, td {
      border: 1px solid #aaa;
      border-collapse: collapse;
      padding: 10px;
    }
    
    tbody {
      th {
        vertical-align: top;
        text-align: right;
      }
    }
  }
}

.section {
  &-container {
    padding: 40px 16px;
    
    > div {
      margin-bottom: 30px;
      
      &:last-child, > :last-child {
        margin-bottom: 0;
      }
    }
  }
  
  &-outer-container {
    background: rgba(0, 0, 0, 0.6);
  }
  
  &.light {
    background: #f4f4f4;
    color: #222;
    
    @include subpixeled;
  }
  
  &.backdrop {
    background-size: cover;
    background-position: center center;
    background-attachment: fixed;
    
    .section-container-outer {
      font-weight: 300;
      padding: 50px 0;
  
      @include subpixeled;
    }
  }
}

.section-header {
  background: #181818;
  background-position: center;
  background-size: cover;
  color: $white;
  position: relative;
  text-shadow: 0px 1px 3px rgba(0, 0, 0, 0.7);

  &-container {
    padding: 40px 16px;
  }

  &-content {
    font-family: "Helvetica Neue", sans-serif;
  }

  h2 {
    font-size: 42px;
    font-weight: 300;
    line-height: 1.35;
    margin: 0;
    
    a {
      @include transition(opacity 0.3s);

      color: #f1f1f1;
      text-decoration: none;


      &:hover {
        opacity: 0.8;
      }
    }
  }

  .tagline {
    font-size: 18px;
    line-height: 1.6;
    margin-top: 10px;
  }

  &.bordered {
    @include box-shadow(inset 0 2px 0 -1px #353535, inset 0 -2px 0 -1px #353535);
    background-color: #131313;
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
  }

  &.background {
    text-shadow: 0 0 1px rgba(0, 0 , 0, 0.2);

    &:before {
      background: rgba(0, 0, 0, 0.6);
      display: block;
      content: " ";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    > .section-header-container {
      position: relative;
      z-index: 1;
    }
  }

  &.large > .section-header-container {
    padding: 80px 16px;
  }
}
