.page-footer {
  @extend %wrapper;
}

.page-footer-container {
  @extend %container;
}

.page-footer {
  background: #131313;
  color: #666;
  font-family: "Helvetica Neue", sans-serif;
  text-align: center;
  
  @include antialiased;
  
  &-container {
    padding: 50px 16px;
    position: relative;
  }
  
  &.bordered {
    border-top: 1px solid #353535;
  }
  
  ul.page-footer-links {
    font-size: 0;
    list-style: none;
    margin: 0 0 20px;
    padding: 0;
    
    li {
      display: inline-block;
      margin: 0 10px;
      
      a {
        color: #999;
        font-size: 14px;
        text-decoration: none;
        
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
  
  .site-info {
    font-family: sans-serif;
    font-size: 13px;
    font-weight: 300;
    
    @include subpixeled;
  }
}

.page-controls {
  bottom: 5px;
  font-size: 16px;
  height: 18px;
  right: 9px;
  position: fixed;
  text-decoration: none;
  width: 100px;
  z-index: 1337;
  
  @include transform(translateZ(0));
  
  > a {
    @include transition(color 0.3s);
    color: $white-dark;
    display: inline-block;
    float: right;
    height: 18px;
    margin-left: 13px;
    position: relative;
    width: 20px;
    
    &:hover, &.active {
      color: $gray;
    }
    
    .icon {
      @include translate(-50%, -50%);
      left: 50%;
      position: absolute;
      top: 50%;
    }

    &.scroll-to-top {
      @include transition(opacity 0.4s);
      
      .icon-angle-up {
        height: 40px;
        width: 40px;
        margin-top: -2px;
      }

      @media (max-width: 768px) {
        font-size: 44px;
      }
    }
  }
  
  @media (max-width: 768px) {
    bottom: 6px;
  }
}

.control-menu {
  background: rgba(19, 19, 27, 0.8);
  position: fixed;
  height: 100%;
  top: 0;
  left: 0;
  width: 100%;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.7);
  
  a {
    @include transition(color .3s);

    color: #fff;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.7);
    
    &:hover {
      color: $white-dark;
    }
  }
  
  &-title {
    font-family: "Helvetica", "Helvetica Neue", "Lato", sans-serif;
    font-size: 42px;
    text-align: center;
  }
  
  &-container {
    @include translate(-50%, -50%);
    @include border-box;
    left: 50%;
    top: 50%;
    max-width: 480px;
    padding: 12px 12px 74px;
    position: absolute;
    width: 100%;
  }
  
  &-nav {
    a {
      border-top: 1px solid $white-dark;
      display: block;
      font-family: "Lato", "Helvetica Neue";
      font-size: 36px;
      font-weight: 300;
      padding: 0 5px;
      
      span {
        display: inline-block;
        text-align: center;
        width: 36px;
      }
      
      &:last-child {
        border-bottom: 1px solid $white-dark;
      }
    }
  }
  
  &.is-visible {
    opacity: 1;
  }
}
