.gallery ul {
  color: $white;
  font-family: "Lato", sans-serif;
  list-style: none;
  margin: 0 auto;
  padding: 0;
  
  @include antialiased;
  
  li {
    display: block;
    float: left;
    margin: 0;
    width: 25%;
    overflow: hidden;
    padding-top: 15%;
    position: relative;
    
    & > a {
      display: block;
      height: 100%;
    }
    
    img {
      @include translate(-50%, -50%);
      @include transition(0.2s);
      
      -moz-backface-visibility: hidden;
      -webkit-backface-visibility: hidden;
      
      width: 101%;
      left: 50%;
      position: absolute;
      top: 50%;
      
      &.fade:hover {
        opacity: 0.7;
      }
    }
    
    .caption {
      @include transition(0.4s);
      
      background: rgba(0, 0, 0, 0.6);
      font-size: 17px;
      height: 100%;
      opacity: 0;
      position: absolute;
      top: 0;
      text-align: center;
      //text-shadow: 1px 1px 22px rgba(0, 0, 0, 0.7);
      width: 100%;
      
      .caption-cell {
        @include border-box;
        @include translate(0, -50%);
        
        padding: 0 10px;
        position: absolute;
        top: 50%;
        width: 100%;
      }
      
      a {
        @include transition(0.3s);
        
        display: inline-block;
        line-height: 1.2;
        color: #fff;
        font-weight: bold;
        text-shadow: 1px 1px 6px rgba(0, 0, 0, 0.7);
        &:hover {
          color: $white-dark;
        }
      }
    }
    @media (max-width: 960px) {
      padding-top: 18%;
      width: 33.3333%;
    }
    @media (max-width: 768px) {
      padding-top: 28%;
      width: 50%;
      
      .caption {
        font-size: 18px;
      }
    }
    @media (max-width: 540px) {
      .caption {
        font-size: 14px;
      }
    }
    @media (max-width: 480px) {
      padding-top: 50%;
      width: 100%;
      
      .caption {
        font-size: 18px;
      }
    }
  }
  
  &:after {
    display: block;
    content: " ";
    clear: both;
  }
}

.gallery.zoom ul li.is-active .caption, .gallery.zoom ul li:hover .caption {
  opacity: 1;
}

.gallery.zoom ul li.is-active img, .gallery.zoom ul li:hover img {
  width: 130%;
}
