@media (max-width: 768px) {
  %wrapper {
    min-width: 0 !important;
  }
  %container {
    width: 100% !important;
  }
}

/* Don't Need These Yet */
$break-points: (
  'large': 1200px,
  'medium': 960px,
  'small': 768px,
  'mobile': 480px
);

$break-points: (
  'small': 768px,
  'mobile': 480px
);

@each $name, $width in $break-points {
  // Default Layout
  @media (min-width: $width) {
    .row-#{$name} {
      > [class^="col-"], .col {
        margin-bottom: 30px;
        padding-left: 0;
        padding-right: 0;
        
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
  
  // Become Row at screen < size
  @media (max-width: $width) {
    .row-#{$name} {
      @include display-flex;
      @include flex-direction(row);
      margin-left: -7.5px;
      margin-right: -7.5px;
      
      > [class^="col-"], .col {
        min-height: 1px;
        position: relative;
        padding-left: 7.5px;
        padding-right: 7.5px;
      }
    }
  }
  
  // Break Row at screen < size2
  @media (max-width: $width) {
    .row-break-#{$name} {
      display: block;
      margin-left: 0;
      margin-right: 0;
      
      > [class^="col-"], .col {
        width: 100%;
        margin-bottom: 30px;
        padding-left: 0;
        padding-right: 0;
        
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
}
