@import "mixins";

.jp {
  font-family: 'ヒラギノ角ゴ Pro W3', 'Hiragino Kaku Gothic Pro', メイリオ, Meiryo, Osaka, 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif;
  font-weight: normal;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.text-center {
  text-align: center;
}

.lowercase {
  text-transform: lowercase;
}

.uppercase {
  text-transform: uppercase;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

.no-border {
  border: 0;
}

.darken {
  opacity: 0.85;
}

a.darken:hover, a:hover .darken {
  opacity: 1;
}
