@import "jass/main";

#page-wrap {
  @include display-flex;
  @include flex-direction(column);
  min-height: 100vh;
}

.page-main {
  @include flex(1);
}

#nav-wrap {
  @extend %wrapper;
}

#nav-wrap ul {
  @extend %container;
}

#nav-wrap {
  background: #202121;

  ul {
    font-family: "Lato", "Helvetica", "Helvetica Neue", sans-serif;
    font-size: 16px;
    list-style: none;
    padding: 16px;
    margin: 0px auto;
    
    li {
      float: right;
      margin: 0 0 0 50px;
      color: #fff;
      
      a {
        color: #aaa;
        text-decoration: none;
        
        &:hover {
          color: #fff;
        }
      }
      
      &.home {
        float: left;
        font-size: 14px;
        font-weight: bold;
        line-height: 28px;
        margin-left: 0;
        
        a {
          @include transition(opacity 0.3s);

          &:hover {
            opacity: 0.8;
          }
        }
      }

      &.selected a {
        color: #fff;
      }
    }
    
    &:after {
      content: " ";
      display: block;
      clear: both;
    }
  }
}

#attributions {
  .icon-behance {
    height: 22px;
    width: 22px;
  }
  .icon-facebook {
    margin-left: -3px;
  }
}
