.aside {
  font-size: 16px;
  margin-bottom: 30px;
  
  &:last-child {
    margin-bottom: 0;
  }
  
  > img {
    display: block;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
  }
  
  &-list {
    font-family: "Lato", sans-serif;
    list-style: none;
    margin-bottom: 10px;
    padding: 0;
    
    > li {
      margin-bottom: 12px;
      padding-left: 29px;
      position: relative;
      
      .icon {
        @include translate(0, -50%);
        
        height: 16px;
        left: 0;
        line-height: 1.8;
        position: absolute;
        top: 50%;
        width: 16px;
      }
      
      .icon-library {
        height: 17px;
        width: 17px;
      }
    }
  }
  
  &-media {
    display: block;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
  }
  
  &-body {
    border-top: 2px solid $default-accent-color;
    border-bottom: 2px solid $default-accent-color;
    padding: 10px;
  
    margin-top: 15px;
  }
  
  > :last-child {
    margin-bottom: 0;
    
    > :last-child {
      margin-bottom: 0;
    }
  }

  /* .aside substyles
   *
   */

  &.bordered, > .bordered {
    border-top: 2px solid $default-accent-color;
    border-bottom: 2px solid $default-accent-color;
    padding: 16px;
  }

  &.framed, > .framed {
    background: #191919;
    padding: 16px;
    
    .aside-body {
      border: 0;
      padding: 16px 0 0;
      
      p {
        line-height: 1;
      }
    }
  }
  
  &.rounded, > .rounded {
    @include border-radius(5px);
    
    background: #191919;
    border: 0;
    padding: 16px;
    
    .aside-body {
      border-top: 1px solid #111;
      border-bottom: 0;
      margin: 16px -16px 0;
      padding: 16px 16px 0;
      
      p {
        line-height: 1;
      }
    }
  }
  
  &.quote {
    border-top: 2px solid $default-accent-color;
    border-bottom: 2px solid $default-accent-color;
    color: $white-dark;
    font-style: italic;
    padding: 16px;
  
    > p:last-of-type {
      margin: 0;
    }
  
    .source {
      display: block;
      line-height: 1.8;
      text-align: right;
    }
  }
}
