}

@media (max-width: 900px) {
}

@media (max-width: 768px) {
  .page-header, .section-header {
    text-align: center;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 27px;
  }

  h2 {
    font-size: 24px;
  }

  h3 {
    font-size: 21px;
  }

  h4 {
    font-size: 18px;
  }

  h5 {
    font-size: 15px;
  }

  h6 {
    font-size: 12px;
  }
  
  h1, h2, h3, h4, h5, h6 {
    margin: $vertical-spacing 0 div($vertical-spacing, 2);
  }
  
  .page-header-container,
  .section-header-container {
    padding-bottom: 30px;
    padding-top: 30px;
  }
    
  .section-container,
  .page-footer-container {
    padding: 35px 16px;
  }

  .page-header h1,
  .section-header h2 {
    font-size: 36px;
  }
}