@charset "utf-8";

$vertical-spacing: 20px;

// Misc & Helper Classes
//@import "misc/buttons";
@import "misc/helpers/main";
@import "misc/colors/main";
@import "misc/base";

$default-accent-color: map-get($dark-colors, "blue");

$link-color: map-get($dark-colors, "blue");
$link-color-hover: map-get($colors, "blue");

// Dependencies
@import "plugins/normalize";
//@import "plugins/syntax-highlighter";

// Layout
@import "layout/containers";
@import "layout/grid";
@import "layout/adaptive";

// Core
@import "core/header";
//@import "core/navbar";
@import "core/content";
@import "core/footer";

// Components
//@import "components/article";
@import "components/aside";
//@import "components/form";
@import "components/gallery";
//@import "components/icon";

//$body-background-color: 
