body {
  background: #131313;
  color: #c6c6c6;
  font: 20px/1.5 "Lato", "Helvetica Neue", Helvetica, sans-serif;
  margin: 0;

  @include antialiased;
}

#page-wrap {
  background: #131313;
}

a {
  color: $link-color;
  text-decoration: none;
  
  &:hover {
    color: $link-color-hover;
  }
}

// Themes
//@import "themes/light";
//@import "themes/dark";

@import "components/adaptive";

.icon {
  fill: currentColor;
  height: 20px;
  width: 20px;
  
  &-inline {
    vertical-align: middle;
  }
}
