h1 {
  font-size: 36px;
}

h2 {
  font-size: 30px;
}

h3 {
  font-size: 24px;
}

h4 {
  font-size: 18px;
}

h5 {
  font-size: 14px;
}

h6 {
  font-size: 12px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 500;
  line-height: 1;
  margin: ($vertical-spacing * 2) 0 div($vertical-spacing, 2);

  &.bordered {
    border-bottom: 1px solid #6a6a6a;
    margin-bottom: div($vertical-spacing, 2);
    padding-bottom: 5px;
  }

  &:first-child {
    margin-top: 0 !important;
  }

  &.section-subtitle {
    color: $white-dark;
    font-size: 18px;
  }

  > big {
    font-size: 1.15em;
  }

  > .subtitle {
    color: $white-dark;
    font-size: 0.75em;
  }

  > small, > .subtext {
    color: $white-dark;
    font-weight: 400;
    font-size: 0.75em;
  }
}

p {
  line-height: 1.8;
  margin: 0 0 $vertical-spacing;
  
  &.lead {
    font-size: 24px;
    font-weight: 300;
  }
}

ul, ol {
  //line-height: 1.8;
  margin: 0 0 $vertical-spacing;
  padding: 0 0 0 25px;

  li {
    line-height: 1.8;
    margin-bottom: div($vertical-spacing, 2);
  }
  
  &.split-2 {
    -webkit-column-count: 2;
    -moz-column-count: 2;
    column-count: 2;

    list-style-position: inside;
    padding-left: 1.5em;
  
    > li {
      &:before {
        content: " ";
      }
    }
  }
}

p:last-child, ul:last-child, ol:last-child, li:last-child {
  margin-bottom: 0;
}

/**
 * Blockquotes
 */
blockquote {
  color: $white-dark;
  border-left: 2px solid $white-dark;
  font-size: 14px;
  padding: 0 div($vertical-spacing, 2);
  margin: $vertical-spacing 0;
  //letter-spacing: -1px;

  > p {
    margin: 0 0 div($vertical-spacing, 2);
  }

  > :last-child {
    margin-bottom: 0;
  }
}

/**
 * Code formatting
 */
pre,
code {
    font-size: 15px;
    background-color: #222;

    @include subpixeled;
}

p code {
  padding: 3px 5px;

  @include border-radius(3px);
}

pre {
  line-height: 1.5;
  padding: 8px 12px;
  margin: $vertical-spacing 0;
  overflow-x: auto;

  > code {
    border: 0;
    padding-right: 0;
    padding-left: 0;
  }
}
