.page-header {
  @extend %wrapper;
}

.page-header-container {
  @extend %container;
}

.page-header {
  background-position: center center;
  background-size: cover;
  border-bottom: 1px solid #353535;
  color: $white;
  font-size: 18px;
  position: relative;
  text-shadow: 0px 1px 3px rgba(0, 0, 0, 0.7);
  
  //@include box-shadow(inset 0 -2px 0 -1px #000);

  &-container {
    padding-bottom: 45px;
    padding-top: 45px;
  }

  h1 {
    font-size: 42px;
    font-weight: 300;
    line-height: 1.35;
    margin: 0;
  
    a {
      @include transition(opacity .3s);
      color: #f1f1f1;
      text-decoration: none;
    
      &:hover {
        opacity: 0.8;
      }
    }
  }
  
  p {
    font-size: 18px;
    margin-top: 10px;
  }
  
  &.large {
    > .page-header-container {
      padding-bottom: 80px;
      padding-top: 80px;
    }
  }
  
  &.background {
    text-shadow: 0 0 1px rgba(0, 0, 0, 0.2);
    
    > .page-header-container {
      padding: 80px 16px;
      position: relative;
      z-index: 1;
    }
    
    &:after {
      background: rgba(0, 0, 0, 0.6);
      display: block;
      content: " ";
      height: 100%;
      left: 0;
      position: absolute;
      top: 0;
      width: 100%;
    }
    
    h1, h2 {
      font-size: 48px;
    }
  }
  
  &.no-border {
    border-bottom: 0;
    box-shadow: none;
    -moz-box-shadow: none;
    -webkit-box-shadow: none;
  }
}
